import React from 'react';
import ReactTypingEffect from 'react-typing-effect';

const HomePage = () => {
    return (
        <div id="main" className="row" style={{height:"100vh", width:"100%"}} data-aos="fade-up" >
            <div className="col-md-2"></div>
            <div className="col-md-10 d-flex align-items-center">
                <div className="container" data-aos="zoom-in" data-aos-delay="100">
                    <h1>Anish Khan</h1>
                    <p>I'm <ReactTypingEffect 
                            speed="200"
                            eraseDelay="1000" 
                            text={[" Accountant!"]}
                        />
                    </p>
                    <div className="social-links">
                        <a href="https://twitter.com/" className="twitter"><i className="bx bxl-twitter"></i></a>
                        <a href="https://www.facebook.com/profile.php?id=100004201801057"><i className="bx bxl-facebook"></i></a>
                        <a href="https://www.instagram.com/anish_rulez/" className="instagram"><i className="bx bxl-instagram"></i></a>
                        <a href="https://www.linkedin.com/in/anish-khan-9056641b5/" className="linkedin"><i className="bx bxl-linkedin"></i></a>
                        <a href="mailto:anishkhan0903@gmail.com" ><i className="bx bxs-envelope"></i></a>
                    </div>
                </div>
            </div>   
        </div>
    );
}

export default HomePage;
